body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  width: 100%;
  height: 100%;
}

#root{
  width:100%;
  height:100%;
}
.main{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    width:70%;
    height: 100%;
    border-right-width: 0.1em;
    border-right-color: gold;
    border-right-style: dotted;
}

.nameComponent{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-left: 2%;
    width: 100%;
    height:95%;

    text-align: center
}

.bigName{
    font-family: 'DAYROM';
    font-size: 8em;
}

.smallName{
    font-family: 'DAYROM';
    font-size: 4em;
}

.nav{
    width: 30%;
    height: 100%;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.navItemContainer{
    width:100%;
    height: 25%;
}

.navitem{
    width:100%;
    text-align: right;
    height: 100%;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content:center;
            justify-content:center;
    background: white;
    border-style: none;
    outline: none;
    cursor: pointer;
    transition-property: background, color; 
    transition-duration: 0.15s;
}


.navitem:hover{
    background:gold;
    color: white;
    
}

.blog{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.blog .title{
    font-family: Coolvetica;
    font-size: 4em;
    color: #1F1F1F;
}

.blogtext{
    font-family: Coolvetica;
    font-size: 1.5em;
    white-space: pre-wrap;
    color: #1F1F1F
}

.blog .date{
    font-family: Coolvetica;
    font-size: 1em;
    color: lightgrey;
    margin-top:-1.1em;
    margin-bottom: 1.5em;
}

img{
    width: 100%;
    height: 20em;
    object-fit: cover;
}

.bbar{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    height: 5%;
}

.button{
    margin-top: 0.25em;
    margin-left: 0.3em;

}


.dNavContainer{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    top: -1px;
    background-color: white;
}

.dNavContainer .alive{
    cursor: pointer;
    transition-property: color; 
    transition-duration: 0.15s;
}

.dNavContainer .item:hover{
    color:#1F1F1F;
}

.dNavContainer .item{
    font-family: 'DAYROM';
    font-size: 1.5em;
}

@font-face {
    font-family: 'DAYROM';
    src: url(/bry/static/media/DAYROM.b03a9848.woff) format('woff');
    font-weight: normal;
    font-style: normal;
  
}


@font-face {
    font-family: 'Coolvetica';
    src: url("/bry/static/media/coolvetica rg.247ed841.woff") format('woff');
    font-weight: normal;
    font-style: normal;
  
}
.content{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  width: 100%;
  font-family: 'DAYROM';
}

.page{
  height: 100%;
  width: 100%;
}

@font-face {
  font-family: 'DAYROM';
  src: url(/bry/static/media/DAYROM.b03a9848.woff) format('woff');
  font-weight: normal;
  font-style: normal;

}


